html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Georama", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  font-family: "Georama", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 80rem;
  margin: 0 auto; }

@media (min-width: 768px) {
  .alinear-derecha {
    display: flex;
    justify-content: flex-end; } }

.dark-mode {
  background-color: #1a1a1a; }
  .dark-mode p {
    color: #ffffff; }
  .dark-mode .formulario p,
  .dark-mode .formulario label,
  .dark-mode .formulario legend {
    color: #ffffff; }
  .dark-mode .formulario input:not([type="submit"]),
  .dark-mode .formulario select,
  .dark-mode .formulario textarea {
    background-color: #333333;
    color: #ffffff;
    border: 1px solid #333333; }
  .dark-mode .formulario ::placeholder,
  .dark-mode .formulario option {
    color: #e1e1e1; }
  .dark-mode a,
  .dark-mode h1,
  .dark-mode h2,
  .dark-mode h3,
  .dark-mode h4 {
    color: #e1e1e1; }
  .dark-mode .anuncio {
    background-color: #333333;
    border: 1px solid #333333; }
  .dark-mode .iconos-caracteristicas img {
    filter: invert(90%); }

/* UI */
@media (min-width: 768px) {
  .main {
    display: grid;
    gap: auto;
    grid-template-columns: repeat(3, 1fr); } }

.main_titulo {
  text-align: center;
  font-family: "Harry Potter", sans-serif; }

.hp-container {
  margin: 1rem auto;
  padding: 0 1rem;
  width: 90%;
  background-color: #946B2D;
  border-radius: 0.75rem; }
  @media (min-width: 768px) {
    .hp-container {
      display: grid;
      gap: 1fr;
      grid-template-columns: repeat(2, 1fr);
      width: 95%; } }
  .hp-container .hp__section {
    text-align: center; }
  .hp-container .hp__image {
    padding: 2rem; }
  .hp-container .hp__image img {
    width: 90%;
    margin: 0 auto;
    height: 30rem; }
    @media (min-width: 768px) {
      .hp-container .hp__image img {
        height: auto;
        width: 100%; } }
  .hp-container .hp__qualities,
  .hp-container .hp__house {
    margin-bottom: 1.5rem; }
  .hp-container h2,
  .hp-container h3,
  .hp-container h4,
  .hp-container p {
    margin-top: 0;
    margin-bottom: 0; }

.vivo {
  color: green; }

.muerto {
  color: red; }
